import Header from "./dashboard-components/Header"
import Sidebar from "./dashboard-components/Sidebar"
import {Redirect } from "react-router"
import Maincontent from "./dashboard-components/Maincontent"
import {useState} from "react"
import Media from "react-media"

function Dashboard (props) {
    const {currentUser, setCurrentUser} = props
    const [activePage, setActivePage] = useState("Welcome")
    const userInfo = JSON.parse(localStorage.getItem('currentUser'))
    const [value,setValue] = useState(0 )
    const [channelName,setChannelName] = useState("")
    const [channelID,setChannelID] = useState()
    const [recipientName, setRecipientName] = useState("") //copy of channelName state for DM
    const [recipientID, setRecipientID] = useState() //copy of channelID state for DM
    const [listOfMessages,setListOfMessages] = useState([])
    const [counter, setCounter] = useState(0)
    const [counter2, setCounter2] = useState(0)
    const [showMenu,setShowMenu] = useState(false)
    const userHeaders = currentUser ? currentUser.headers : {}
    const userId = currentUser.data ? currentUser.data.id : ""
    let greetings = currentUser.data ? `Hello, ${currentUser.data.email}!` : ""

    if(Object.keys(currentUser).length === 0) {
        return <Redirect to="/"/>
    } else {
        return (
            <>

            

            <Media query="(max-width: 1332px)">
                {matches => (matches && 
                    <>
                        <div className="flex flex-col h-screen">
                            <Header mobileView={true} showMenu={showMenu} setShowMenu={setShowMenu} greetings={greetings}/>
                            <div className="flex flex-grow">
                                <Maincontent activePage={activePage} userHeaders={userHeaders} userId={userId} setValue={setValue} value={value} channelName={channelName} recipientName={recipientName} channelID={channelID} recipientID={recipientID} listOfMessages={listOfMessages} setListOfMessages={setListOfMessages} setActivePage={setActivePage} counter = {counter} setCounter = {setCounter} mobileView={true} setChannelName={setChannelName} setRecipientName={setRecipientName} setChannelID={setChannelID} setRecipientID={setRecipientID} showMenu={showMenu} setShowMenu={setShowMenu} setCurrentUser={setCurrentUser} setShowMenu={setShowMenu} counter2={counter2} setCounter2={setCounter2}/>
                                <Sidebar userHeaders={userHeaders} setActivePage={setActivePage} value={value} setChannelName={setChannelName} setRecipientName={setRecipientName} setChannelID={setChannelID} setRecipientID={setRecipientID} setListOfMessages={setListOfMessages} counter = {counter} setCounter = {setCounter} showMenu={showMenu} mobileView={true} setShowMenu={setShowMenu} counter2= {counter2} setCounter2={setCounter2} greetings={greetings} currentUser={currentUser} setCurrentUser={setCurrentUser}/>
                            </div>
                        </div> 
                    </>

                )}
            </Media>

            <Media query="(min-width: 1333px)">
                {matches => (matches && 
                
                    <div className="flex flex-col h-screen">
                        <Header mobileView={false} showMenu={showMenu} setShowMenu={setShowMenu} greetings={greetings}/>
                        <div className="flex flex-grow">
                            <Sidebar userHeaders={userHeaders} setActivePage={setActivePage} value={value} setChannelName={setChannelName} setRecipientName={setRecipientName} setChannelID={setChannelID} setRecipientID={setRecipientID} setListOfMessages={setListOfMessages} counter = {counter} setCounter = {setCounter} mobileView = {false} showMenu={showMenu} setShowMenu={setShowMenu} counter2={counter2} setCounter2={setCounter2} greetings={greetings} currentUser={currentUser} setCurrentUser={setCurrentUser}/>
                            <Maincontent activePage={activePage} userHeaders={userHeaders} userId={userId} setValue={setValue} value={value} channelName={channelName} recipientName={recipientName} channelID={channelID} recipientID={recipientID} listOfMessages={listOfMessages} setListOfMessages={setListOfMessages} setActivePage={setActivePage} counter = {counter} setCounter = {setCounter} mobileView={false} setCurrentUser={setCurrentUser} setShowMenu={setShowMenu} counter2={counter2} setCounter2={setCounter2}/>
                        </div>
                    </div> 
                
                )}
            </Media>

            </>

        )
    }

}
export default Dashboard